import React from 'react';
import { Link } from 'react-router-dom';
import Paper from '@material-ui/core/Paper';
import EntityContainer from 'modules/entity/containers';
import Helmet from 'react-helmet';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import Typography from '@material-ui/core/Typography';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import { ErrorBoundary, KntButton, Loader } from 'shared/components';
import Skeleton from '@material-ui/lab/Skeleton';
import Grid from '@material-ui/core/Grid';
import dayjs from 'dayjs';
import { HtmlParser } from 'shared/components';
import { useAppState } from 'shared/state';
import { useRouter } from 'shared/hooks';
import { NewsEntity } from 'shared/typings/news.types';

const getFormat = (filename: string) => filename.split('.').pop();

const renderFiles = (file: any) => {
	if (file.thumbnail) {
		return (
			<div className="files-display__img">
				<img src={get(file, 'url')} alt="" />
			</div>
		);
	} else if (getFormat(file.name) === 'wav' || getFormat(file.name) === 'mp3') {
		return (
			<audio controls>
				<source src={file && file.url} type="audio/wav" />
				<source src={file && file.url} type="audio/mpeg" />
				Your browser does not support the audio element.
			</audio>
		);
	} else {
		return <span>{file.name}</span>;
	}
};

const getLink = (item: NewsEntity, companyId: number, campaignId: number) => {
	if (item?.receiver?.campaign) return `/news/campaigns/view/${campaignId}`;
	return '/news/news-list';
};

const getLinkName = (item: NewsEntity) => {
	if (item?.receiver?.campaign) return `${get(item, 'receiver.campaign.name')} news list`;
	return 'News List';
};

// eslint-disable-next-line no-unused-vars
const NewsView = (props: any) => {
	const { isPermitted, companyId } = useAppState();
	const { params } = useRouter();

	const campaignId = parseInt((params as { campaignId: string })?.campaignId);
	const newsId = parseInt((params as { newsId: string })?.newsId);

	return (
		<EntityContainer.One
			id={newsId}
			entity="news"
			name={`SingleNews-${newsId}`}
			url={`/${companyId}/notifications/${newsId}/`}
			params={{}}
			relations=""
		>
			{({ item, isFetched }) => {
				const newsItem = item as NewsEntity;
				const newsClientGroups = newsItem?.receiver?.campaign?.categories;
				return (
					<div className="news-page mb-2.5 py-6">
						<Helmet>
							<title>KENNEKT | {`${newsItem.subject}`}</title>
							<meta name="description" content="KENNEKT Announcement" />
						</Helmet>
						<div className="flex-center-between mb-4">
							{isFetched ? (
								<h1 className="text-2xl font-bold dark:text-white">{newsItem.subject}</h1>
							) : (
								<div className="w-[200px]">
									<Skeleton />
								</div>
							)}
							<KntButton>
								<Link
									to={
										campaignId
											? `/news/campaigns/edit/${campaignId}/${newsId}`
											: `/news/edit/${newsId}`
									}
								>
									Edit News
								</Link>
							</KntButton>
						</div>
						<div className="news-breadcrumb mb-5">
							{isFetched ? (
								<Breadcrumbs aria-label="breadcrumb">
									<Link color="inherit" to={getLink(newsItem, companyId as number, campaignId)}>
										<span className="text-primary transition hover:text-primary-dark">
											{getLinkName(newsItem)}
										</span>
									</Link>
									<Typography color="textPrimary">{newsItem.subject}</Typography>
								</Breadcrumbs>
							) : (
								<div className="w-[200px]">
									<Skeleton />
								</div>
							)}
						</div>
						<div className="uni-page__content">
							<Paper className="uni-page__paper">
								<div className="p-5">
									<div className="mb-5">
										<Grid container spacing={3}>
											<Grid item xs={12}>
												<div className="data-list">
													<div className="data-list__row news-view-row">
														<div className="data-list__prop">Author: </div>
														{isFetched ? (
															<div className="data-list__value">{get(item, 'author.name', '')}</div>
														) : (
															<div className="w-[200px]">
																<Skeleton />
															</div>
														)}
													</div>
													{isPermitted && (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Status: </div>

															{isFetched ? (
																<div className="data-list__value">{get(item, 'status', '')}</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													)}

													{isPermitted && (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Frequency: </div>

															{isFetched ? (
																<div className="data-list__value">{get(item, 'frequency', '')}</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													)}
													{isPermitted && (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Timezone: </div>

															{isFetched ? (
																<div className="data-list__value">
																	{get(item, 'timezone.title', '')}
																</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													)}
													{isPermitted && (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Start Time: </div>

															{isFetched ? (
																<div className="data-list__value">
																	{dayjs(get(item, 'start_time', '')).format('YYYY-MM-DD HH:mm')}
																</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													)}

													{isPermitted && (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Allow Case Creation: </div>
															{isFetched ? (
																<div className="data-list__value">
																	{get(item, 'open_for_cases') ? 'Yes' : 'No'}
																</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													)}

													{newsItem?.receiver?.staff_departments?.length ? (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Related Departments: </div>
															{isFetched ? (
																<div className="data-list__value">
																	{newsItem?.receiver?.staff_departments?.map((dep, index) => (
																		<span key={index}>{`${dep.name}${
																			newsItem?.receiver?.staff_departments?.length - 1 !== index
																				? ', '
																				: ''
																		}`}</span>
																	))}
																</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													) : null}
													{newsClientGroups?.length ? (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Related Client Groups: </div>
															{isFetched ? (
																<div className="data-list__value">
																	{newsClientGroups.map((clg, index) => (
																		<span key={index}>{`${clg.name}${
																			newsClientGroups.length - 1 !== index ? ', ' : ''
																		}`}</span>
																	))}
																</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													) : null}

													{get(item, 'department.name') && (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Department: </div>
															{isFetched ? (
																<div className="data-list__value">
																	{get(item, 'department.name')}
																</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													)}
													{get(item, 'sub_department.name') && (
														<div className="data-list__row news-view-row">
															<div className="data-list__prop">Sub department: </div>
															{isFetched ? (
																<div className="data-list__value">
																	{get(item, 'sub_department.name')}
																</div>
															) : (
																<div className="w-[200px]">
																	<Skeleton />
																</div>
															)}
														</div>
													)}
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
								<div className="mb-5 pl-5">
									<Typography variant="h4" gutterBottom>
										News content
									</Typography>
								</div>
								<div className="mb-5 p-5">
									{/* @ts-ignore */}
									{isFetched ? <HtmlParser text={newsItem.body} /> : <Loader tiled withOverlay />}
								</div>
								<div className="p-5">
									{!isEmpty(get(newsItem, 'files', [])) && (
										<div className="data-list__row mb-2.5  flex">
											<div className="data-list__prop">Files: </div>
											<div className="data-list__value">
												<div className="files-display">
													{get(newsItem, 'files', []).map((file: any) => {
														return (
															<div className="files-display__item mb-5" key={file.name}>
																{renderFiles(file)}
															</div>
														);
													})}
												</div>
											</div>
										</div>
									)}
								</div>
							</Paper>
						</div>
					</div>
				);
			}}
		</EntityContainer.One>
	);
};

export default function NewsViewWithErrorBoundary(props: any) {
	return (
		<ErrorBoundary>
			<NewsView {...props} />
		</ErrorBoundary>
	);
}
